<template>
    <qiong-loading></qiong-loading>
</template>

<script>
import QiongLoading from '../../../components/Qiong/Ui/QiongLoading.vue'
    export default {
        components: { QiongLoading },
        name: '',
        data() {
            return {
                
            }
        },
        mounted() {
            const query = {
                'menu': false
            }
            const id = this.$route.query.unid
            
            if (id) {
                query.unid = id
            }
            setTimeout(() => {
                this.$router.push({
                    path: '/ui/pdf_user/',
                    query
                })
            }, 400)
        },
    }
</script>

<style lang="sass" scoped>
    
</style>


